import { Component, Prop, State, h } from '@stencil/core';

@Component({
  tag: 'app-button',
  styleUrl: 'button.component.scss',
  shadow: true,
})
export class ButtonComponent {
  @Prop() classButton: string = 'primary';
  @Prop() disabled: boolean = false;
  @Prop() buttonType: string = 'button';

  @State() mouseY: number = 0;
  @State() mouseX: number = 0;
  @State() showEffect: boolean = false;

  setPosition(e: MouseEvent) {
    this.showEffect = false;
    this.mouseY = e.offsetY;
    this.mouseX = e.offsetX;

    setTimeout(() => {
      this.showEffect = false;
    }, 1000);
  }

  render() {
    return (
      <div class={'dav-buttonContainer' + (this.classButton === 'iconSmall' ? '--row' : '')}>
        <button
          class={'dav-button--' + this.classButton}
          type={this.buttonType}
          disabled={this.disabled}
          name="button"
          onClick={(e: any) => this.setPosition(e)}
        >
          <slot></slot>
          <slot name="icon"></slot>
          {this.showEffect && this.classButton != 'iconSimple' && this.classButton != 'back' &&
            <span
              class="dav-button__ripple"
              style={{ '--mouse-y': this.mouseY + 'px', '--mouse-x': this.mouseX + 'px' }}
            ></span>
          }
        </button>
        {(this.classButton === 'iconSmall' || this.classButton === 'iconLarge' || this.classButton === 'iconLargeGrey') &&
          <div class={'dav-button__text' + (this.classButton === 'iconSmall' ? '--small' : '')}>
            <slot name="iconText"></slot>
          </div>
        }
      </div>
    );
  }  
}
